import React from 'react';
import { connect } from 'react-redux';
import { logout } from '../../actions/UserActions';
import { Link, NavLink, withRouter } from 'react-router-dom';
import { userService } from '../../services/user.service';
import './AppHeader.scss';

 class _AppHeader extends React.Component {
    state = {
        showNav: false,
        user: null
    }

    get dropNavClass() {
        return (this.state.showNav) ? 'drop-nav' : '';
    }

    async componentDidMount() {
        const user = await userService.getUser();
        this.setState({ user });
    }

    async componentDidUpdate() {
        const user = await userService.getUser();
        if (this.state.user !== user) {
            this.setState({ user });
        }
    }

    toggleNav = () => {
        this.setState(prevState => ({showNav: !prevState.showNav}))
    }

    closeNav = () => {
        this.setState({showNav: false})
    }

    doLogout = async () => {
        const user = await this.props.logout();
        this.setState({ user })
        this.props.history.push('/')
        this.closeNav();    
    }

    render() {
        return (
            <header className="app-header flex space-between align-center">
                <Link to="/home">
                    <div className="logo">
                        mister BITCoin
                    </div>
                </Link>
                <nav className={ `main-nav ${this.dropNavClass}` }>
                    <NavLink activeClassName="active" to="/home" exact onClick={ this.closeNav }> Home </NavLink>
                    <NavLink activeClassName="active" to="/contact" exact onClick={ this.closeNav }> Contact </NavLink>
                    <NavLink activeClassName="active" to="/stats" onClick={ this.closeNav }> Stats </NavLink>
                    { this.state.user && <button onClick={ this.doLogout }> Logout </button> }
                </nav>
                <button className={ `hamburger flex column ${this.dropNavClass}` } onClick={ this.toggleNav }>
                    <span></span>
                    <span></span>
                    <span></span>
                </button>
                <div className={ `screen ${this.dropNavClass}` } onClick={ this.closeNav }></div>
            </header>
        )
    }
}


function mapStateProps(state) {
    return {
        users: state.userReducer.users,
        user: state.userReducer.user
    }
}

const mapDispatchToProps = {
    logout
}

export const AppHeader = withRouter(connect(mapStateProps, mapDispatchToProps)(_AppHeader))
