import React, { Component } from 'react';
import { connect } from 'react-redux';
import { loadUser } from '../../actions/UserActions';
import { Link } from 'react-router-dom';
import { bitcoinService } from '../../services/bitcoin.service';
import { MoveList } from '../../cmps/MoveList/MoveList';
import { Loader } from '../../cmps/Loader/Loader';
import './HomePage.scss';


class _HomePage extends Component {
    state = {
        bitRate: 0
    }
    async componentDidMount() {
        await this.props.loadUser();
        if (!this.props.user) return this.props.history.push('/');
        const bitRate = await bitcoinService.getRate(1);
        this.setState({ bitRate: bitRate.data });
    }
    render() {
        if (!this.props.user || !this.state.bitRate) return <Loader />
        const { name, coins, moves } = this.props.user;
        return (
            <main className="home-page flex column align-center">
                <h1> Welcome, { name }! </h1>
                <Link to="/contact">
                    <button>
                        Get Started 
                    </button>
                </Link>
                <div className="user-details flex justify-center">
                    <p> <i className="fas fa-coins"></i> Your Coins: { coins } </p>
                    <p> <i className="fab fa-bitcoin"></i> BTC: { this.state.bitRate } </p>
                </div>
                <MoveList moves={ moves } />
            </main>
        )
    }
}

function mapStateProps(state) {
    return {
        user: state.userReducer.user
    }
}

const mapDispatchToProps = {
    loadUser
}

export const HomePage = connect(mapStateProps, mapDispatchToProps)(_HomePage)
