import React, { Component } from 'react';
import { connect } from 'react-redux';
import { removeContact, loadContacts } from '../../actions/ContactActions';
import { loadUser, addMove } from '../../actions/UserActions';
import { contactService } from '../../services/contact.service'
import { TransferFunds } from '../../cmps/TransferFunds/TransferFunds';
import { MoveList } from '../../cmps/MoveList/MoveList';
import { Loader } from '../../cmps/Loader/Loader';
import './ContactDetails.scss';

class _ContactDetails extends Component {
    state = {
        contact: null,
        user: null
    }

    async componentDidMount() {
        this.props.loadContacts();
        const contact = await contactService.getContactById(this.props.match.params.id)
        const user = await this.props.loadUser();
        if (!user) this.props.history.push('/');
        this.setState({ contact, user })
    }

    deleteContact = async () => {
        await this.props.removeContact(this.state.contact._id);
        this.props.history.push('/contact');
    }

    onAddMove = async (ev, amount) => {
        ev.preventDefault();
        const user = await this.props.addMove(this.state.contact, amount);
        this.setState({ user })
    }

    render() {
        if (!this.state.contact || !this.props.user) return <Loader />
        const { _id, name, phone, email } = this.state.contact;
        const { user } = this.state
        return (
            <main className="contact-details">
                <h2 className="header"> Contact Details </h2>
                <div className="details flex column align-center">
                    <h3> { name } </h3>
                    <img src={`https://robohash.org/${_id}?set=set5`} alt="contact img" />
                    <div className="info">
                        <p className="flex align-center"> 
                            <i className="fas fa-phone-square-alt"> </i> 
                            <span>Phone: </span> { phone } 
                        </p>
                        <p className="flex align-center"> 
                            <i className="fas fa-envelope"></i> 
                            <span>Email: </span> { email } 
                        </p>
                    </div>
                </div>
                <div className="actions">
                    <button onClick={() => this.props.history.push('/contact')}> Go back </button>
                    <button onClick={ this.deleteContact }> Delete </button>
                    <button onClick={ () => this.props.history.push(`/contact/edit/${_id}`) }> Edit </button>
                </div>
                <TransferFunds user={ user } contact={ this.state.contact } onAddMove={ this.onAddMove } />
                <MoveList contact={ this.state.contact } moves={ user.moves } />
            </main>
        )
    }
}

function mapStateProps(state) {
    return {
        contacts: state.contactReducer.contacts,
        user: state.userReducer.user
    }
}

const mapDispatchToProps = {
    removeContact,
    loadContacts,
    loadUser,
    addMove
}

export const ContactDetails = connect(mapStateProps, mapDispatchToProps)(_ContactDetails)