import React, { Component } from 'react'
import { connect } from 'react-redux';
import { login, signup, loadUsers, loadUser } from '../../actions/UserActions';
import './LoginPage.scss'

class _LoginPage extends Component {
    state = {
        name: '',
        showError: false,
        errorMsg: '',
        loginMode: true,
    }

    async componentDidMount() {
        await this.props.loadUsers();
        await this.props.loadUser();
        if (this.props.user) this.props.history.push('/home');
    }

    handleLogin = async (ev) => {
        ev.preventDefault();
        if (!this.state.name) return this.setState({ showError: true, errorMsg: 'Field is required.' })
        const user = (this.state.loginMode) ? 
                        await this.props.login(this.state.name) : 
                        await this.props.signup(this.state.name);
        if (user) this.props.history.push('/home');
        else this.setState({showError: true, errorMsg: 'Incorrect name, try again.'})
    }

    handleChange = ({ target }) => {
        this.setState({ name: target.value, showError: false });
    }

    toggleLogin = (ev) => {
        ev.preventDefault();
        this.setState( ({ loginMode }) => ({ loginMode: !loginMode }) )
        this.setState({ name: '', showError: false })
    }

    render() {
        const { loginMode } = this.state;
        return (
            <main className="login-page">
                <h2> { (loginMode ? 'Login' : 'Signup') } </h2>
                <form className="login-form flex column align-center" onSubmit={ this.handleLogin }>
                    <input type="text" value={ this.state.name } name="name" onChange={ this.handleChange } placeholder="Please enter your name..." />
                    {this.state.showError && <small> { this.state.errorMsg } </small> }
                    <button> { (loginMode ? 'Login' : 'Signup') } </button>
                    <button onClick={ this.toggleLogin }> or { (!loginMode ? 'Login' : 'Signup') } </button>
                </form>
            </main>
        )
    }
}


function mapStateProps(state) {
    return {
        users: state.userReducer.users,
        user: state.userReducer.user
    }
}

const mapDispatchToProps = {
    login,
    signup,
    loadUsers,
    loadUser
}

export const LoginPage = connect(mapStateProps, mapDispatchToProps)(_LoginPage)