import { userService } from '../services/user.service'

// Action Creators ----------------------------------------------------
const _setUsers = (users) => ({ type: 'SET_USERS', users });
const _setUser = (user) => ({ type: 'SET_USER', user });
const _addMove = (user) => ({ type: 'ADD_MOVE', user });
const _login = (user) => ({ type: 'LOGIN', user });
const _signup = (user) => ({ type: 'SIGNUP', user });
const _logout = (user) => ({ type: 'LOGOUT', user });
// --------------------------------------------------------------------

// Action Dispatchers -------------------------------------------------
export function loadUsers() {
    return async (dispatch) => {
        const users = await userService.getUsers();
        dispatch(_setUsers(users))
    }
}
export function loadUser() {
    return async (dispatch) => {
        const user = await userService.getUser()
        dispatch(_setUser(user))
        return user;
    }
}

export function addMove(contact, amount) {
    return async (dispatch) => {
        const user = await userService.addMove(contact, amount)
        dispatch(_addMove(user))
        return user;
    }
}

export function login(name) {
    return async (dispatch) => {
        const user = await userService.login(name)
        dispatch(_login(user))
        return user
    }
}

export function signup(name) {
    return async (dispatch) => {
        const user = await userService.signup(name)
        dispatch(_signup(user))
        return user;
    }
}

export function logout() {
    return (dispatch) => {
        return userService.logout()
            .then((user) => dispatch(_logout(user)))
    }
}

// --------------------------------------------------------------------