import React, { Component } from 'react'
import { connect } from 'react-redux';
import { saveContact, loadContacts } from '../../actions/ContactActions';
import { contactService } from '../../services/contact.service'
import { Link } from 'react-router-dom'
import { Loader } from '../../cmps/Loader/Loader';
import './ContactEdit.scss'

class _ContactEdit extends Component {
    state = {
        contact: null,
        showError: false,
        errorMsg: 'All fields are required!'
    }

    async componentDidMount() {
        const { id } = this.props.match.params
        const contact = id ? await contactService.getContactById(id) : contactService.getEmptyContact();
        this.setState({ contact });
        this.props.loadContacts();
    }

    saveContact = async (ev) => {
        ev.preventDefault();
        const { name, phone, email } = this.state.contact;
        if (!name || !phone || !email ) return this.setState({ showError: true })
        await this.props.saveContact(this.state.contact)
        this.props.history.goBack();
    }

    handleChange = ({ target }) => {
        this.setState({ showError: false })
        const field = target.name;
        const value = target.value;
        this.setState( ({ contact }) => ({contact: {...contact, [field]: value}}))
    } 

    render() {
        if (!this.state.contact) return <Loader />
        const { _id, name, phone, email } = this.state.contact
        return (
            <main className="contact-edit">
                <h2> { (_id) ? 'Edit' : 'Add' } Contact </h2>
                <form className="contact-form flex column align-center" onSubmit={ this.saveContact }>
                    { this.state.showError && <small> { this.state.errorMsg } </small> } 
                    <input type="text" name="name" value={ name } onChange={ this.handleChange } placeholder="Contact's Name..." />
                    <input type="text" name="email" value={ email } onChange={ this.handleChange } placeholder="Contact's E-Mail..." />
                    <input type="text" name="phone" value={ phone } onChange={ this.handleChange } placeholder="Contact's Phone..." />
                    <button> Save </button>
                    <Link to='/contact'> Cancel </Link>
                </form>
            </main>
        )
    }
}


function mapStateProps(state) {
    return {
        contacts: state.contactReducer.contacts
    }
}

const mapDispatchToProps = {
    saveContact,
    loadContacts
}

export const ContactEdit = connect(mapStateProps, mapDispatchToProps)(_ContactEdit)