const INITIAL_STATE = {
    user: null,
    users: null
}

export function UserReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case 'SET_USERS':
            return {
                ...state,
                users: action.users
            }            
        case 'SET_USER':
            return {
                ...state,
                user: action.user
            }            
        case 'ADD_MOVE':
            return {
                ...state,
                user: action.user
            }            
        case 'LOGIN':
            return {
                ...state,
                user: action.user
            }            
        case 'SIGNUP':
            state = {
                ...state,
                users: [...state.users, action.user],
                user: action.user
            }
            return state;
        case 'LOGOUT':
            return {
                ...state,
                user: action.user
            }          
        default:
            return state;
    }
}