import React, { Component } from 'react'
import { connect } from 'react-redux';
import { loadContacts, setFilter, removeContact } from '../../actions/ContactActions';
import { loadUser } from '../../actions/UserActions';
import { ContactList } from '../../cmps/ContactList/ContactList';
import { ContactFilter } from '../../cmps/ContactFilter/ContactFilter';
import { Loader } from '../../cmps/Loader/Loader';
import { Link } from 'react-router-dom';
import _ from 'lodash';
import './ContactPage.scss';

class _ContactPage extends Component {
    async componentDidMount() {
        await this.props.loadUser();
        if (!this.props.user) return this.props.history.push('/');
        this.props.setFilter({ term: '' });
        this.props.loadContacts();
        this.onChangeFilter = _.debounce(this.onChangeFilter, 300)
    }

    onChangeFilter = (filterBy) => {
        this.props.setFilter(filterBy);
        this.props.loadContacts();
    }

    onDeleteContact = async (e, id) => {
        e.preventDefault();
        this.props.removeContact(id);
        this.props.loadContacts();
    }

    render() {
        const { contacts } = this.props;
        if (!contacts) return <Loader />
        return (
            <main className="contact-page">
                <h2> Contacts </h2>
                <ContactFilter onChangeFilter={ this.onChangeFilter } />
                <ContactList contacts={ contacts } onDeleteContact={ this.onDeleteContact } />
                <Link to="/contact/edit" className="add-btn flex-center"> <i className="fas fa-plus"></i> </Link>
            </main>
        )
    }
}

function mapStateProps(state) {
    return {
        contacts: state.contactReducer.contacts,
        user: state.userReducer.user
    }
}

const mapDispatchToProps = {
    loadContacts,
    setFilter,
    removeContact,
    loadUser
}

export const ContactPage = connect(mapStateProps, mapDispatchToProps)(_ContactPage)